@import "typography";
@import "backgrounds";
@import "buttons";
@import "header";
@import "footer";
@import "events";
@import "blocks/blocks";
@import "../assets/slick";
@import "../assets/slick-theme";
@import "slick";

*,
input,
select,
textarea,
option,
button:focus-visible {
  outline: none !important;
}

.page-meta {
  #breadcrumbs {
    font-size: 0.875rem;
  }
}

a.anchor {
  display: block;
  position: relative;
  top: -80px;
  visibility: hidden;
}

ul.check {
  li {
    color: black;
    font-weight: 500;
    list-style: none;
    margin-bottom: 0.5rem;
    background: url("../img/check-bullet.svg") no-repeat left center;
    background-size: 18px;
    padding: 5px 10px 5px 25px;
    list-style: none;
    vertical-align: middle;
    font-size: 0.875rem;
    line-height: 1.4;
    margin-bottom: 0.5rem;
  }
}

.padding-top {
  padding-top: 80px;
}

.bg-light,
.scrolled {
  background-color: white !important;
}

$sizes: (
  25: 25%,
  50: 50%,
  75: 75%,
  100: 100%,
);
@each $breakpoint in map-keys($grid-breakpoints) {
  @each $size, $length in $sizes {
    @include media-breakpoint-up($breakpoint) {
      .w-#{$breakpoint}-#{$size} {
        width: $length !important;
      }
    }
  }
}

.step_icon {
  position: relative;
  &__container {
    position: relative;
    padding: 1rem;
  }
  &__step {
    position: absolute;
    top: 0;
    left: 0;
    color: #fff;
    background-color: #000;
    border-radius: 100%;
    width: 2rem;
    height: 2rem;
    padding: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
  }
  &__icon {
    background-color: $pink-100;
    border-radius: 100%;
    aspect-ratio: 1 / 1;
    position: relative;
    padding: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 140px;
    margin: auto;
  }
  &__icon img {
    max-width: 60px;
    max-height: 60px;
  }
  &__title {
    font-weight: bold;
    text-align: center;
    font-size: 1.2rem;
    line-height: 1.1;
    color: $black;
  }
}

.full_video {
  .ratio {
    border-radius: 2rem;
    overflow: hidden;
  }
  &__container {
    position: relative;
    z-index: 0;
    cursor: pointer;

    img {
      width: 100%;
      background-size: cover;
      background-position: center;
      position: relative;
    }

    .play {
      position: absolute;
      z-index: 10;
      display: flex;
      justify-content: center;
      align-items: center;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      pointer-events: none;
      font-size: 3rem;
      transition: all 0.3s ease;
      img {
        width: 80px;
        height: 80px;
      }
    }
    &:hover {
      .play {
        color: #c5d2d8;
      }
    }
  }
}

a.noline {
  text-decoration: none;
}

// fuck Apple
button {
  -webkit-appearance: none !important;
  -webkit-border-radius: 0;
  border-radius: 0;
}

textarea,
input.text,
input[type="text"],
input[type="button"],
input[type="submit"],
input[type="email"],
input[type="tel"],
input[type="number"] {
  -webkit-appearance: none;
  border-radius: 0;
}

.grecaptcha-badge {
  width: 70px !important;
  overflow: hidden !important;
  transition: all 0.3s ease !important;
  left: 4px !important;
}

.grecaptcha-badge:hover {
  width: 256px !important;
}

.filters {
  padding: 1rem 1rem 2rem 1rem;
  border-radius: 1rem;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}
